import styled from "@emotion/styled"
import { useAuth } from "./context/auth-context"
import { ProjectList } from './screens/project-list'
import { ButtonNoPadding, JRow } from "./components/lib"
import { ReactComponent as SoftwareLogo } from './assets/software-logo.svg'
import { Button, Dropdown, Menu } from "antd"
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import { ScreenProject } from "./screens/project"
import { resetRoute } from "./utils"
import { ProjectModal } from "./screens/project-list/project-modal"

import { ProjectPopover } from "./components/project-popover"
import { UserPopover } from "./components/user-popover"
import { ItemType } from "antd/lib/menu/hooks/useItems"
import { LogoutOutlined } from "@ant-design/icons"


export default function AuthenticatedApp() {

  return <Container>
    <Router>
      <PageHeader />
      <Main>
        <Routes>
          <Route path="/projects" element={<ProjectList />}></Route>
          <Route path="/projects/:projectId/*" element={<ScreenProject/>}></Route>
          <Route path="/" element={<Navigate to="/projects" replace={true}/>} /> 
          {/* <Navigate to="/projects"/> */}
        </Routes>
      </Main>
      <ProjectModal></ProjectModal>
    </Router>
  </Container>
}


const PageHeader = () => {
  const  { logout, user } = useAuth()
  const menuItems: ItemType[] = [
    {
      label: '登出',
      key: 'logout',
      icon: <LogoutOutlined />,
      onClick: () => logout()
    }
  ]

  return (
    <Header between={true}>
      <HeaderLight gap={true}> 
        <ButtonNoPadding type="link" onClick={resetRoute}>
          <SoftwareLogo width={'18rem'} color="rgb(30, 132, 255)"/>
        </ButtonNoPadding>
        <ProjectPopover />
        <UserPopover />
      </HeaderLight>
      <HeaderRight>
        <Dropdown overlay={<Menu items={menuItems}/>}>
            <Button type="link" onClick={e => e.preventDefault()}>
              Hi, { user?.name }
            </Button>
        </Dropdown>
      </HeaderRight>
    </Header>
  )
}


const Container = styled.div`
  display: grid;
  grid-template-rows: 6rem 1fr;
  height: 100vh;
`

const Header = styled(JRow)`
  padding: 3.2rem;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
  z-index: 1;
`

const HeaderLight = styled(JRow)``

const HeaderRight = styled.div``

const Main = styled.main`
  display: flex;
  flex-direction: column;
  overflow: hidden;
`