import styled from "@emotion/styled"
import { Button, Drawer, Form, Input, Spin } from "antd"
import { useForm } from "antd/lib/form/Form"
import { useEffect, useState } from "react"
import { ErrorBox } from "../../components/lib"
import { UserSelect } from "../../components/user-select"
import { useAddProjects, useEditProject } from "../../hooks/project"
import { useProjectModal, useProjectQueryKey } from "./utils"

export const ProjectModal = () => {
  const { projectModelOpen, editingProjectData, close, isFetching } = useProjectModal()
  const useMutateProject = editingProjectData ? useEditProject : useAddProjects
  const { mutateAsync, error, isLoading: mutateLoading } = useMutateProject(useProjectQueryKey())

  const [form] = useForm()
  const closeHandler = () => {
    form.resetFields()
    close()
  }
  const onFinish = (values: any) => {
    mutateAsync({...editingProjectData, ...values}).then(closeHandler)
  }

  const title = editingProjectData ? '编辑项目' : '创建项目'

  useEffect(() => {
    form.setFieldsValue(editingProjectData)
  },[editingProjectData, form])


  return (
    <Drawer 
      visible={projectModelOpen} 
      width={'100%'} 
      onClose={close}
      forceRender={true}>
      <Container>
      {
        isFetching ? <Spin size={'large'}/> : <>
          <h1>{title}</h1>
          <ErrorBox error={error} />
          <Form form={form} layout={'vertical'} style={{ width: '40rem'}} onFinish={onFinish}>
            <Form.Item label={'名称'} name={'name'} rules={[{ required: true, message: '请输入项目名称'}]}>
              <Input placeholder="请输入项目名称" />
            </Form.Item>

            <Form.Item label={'部门'} name={'organization'} rules={[{ required: true, message: '请输入部门名称'}]}>
              <Input placeholder="请输入部门名称" />
            </Form.Item>

            <Form.Item label={'负责人'} name={'personId'}>
              <UserSelect defaultOptionName="负责人"></UserSelect>
            </Form.Item>

            <Form.Item>
              <Button loading={mutateLoading} type={'primary'} htmlType={'submit'}>
                提交
              </Button>
            </Form.Item>
          </Form>
        </>
      }
      </Container>
    </Drawer>
  )
}

const Container = styled.div`
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`