import { Button, Divider, List, Modal, Row } from "antd"
import dayjs from "dayjs"
import { useState } from "react"
import { Link } from "react-router-dom"
import { JRow, ScreenContainer } from "../../components/lib"
import { useDeleteEpic, useGetEpics } from "../../hooks/epic"
import { useGetTasks } from "../../hooks/task"
import { Epic } from "../../types/epic"
import { useProjectInUrl } from "../kanban/utils"
import { CreateEpic } from "./create-epic"
import { useEpicSearchParams, useEpicsQueryKey } from "./utils"

export const EpicScreen = () => {
  // 获取当前 project
  const { data: currentProject } = useProjectInUrl()
  const { data: epicData } = useGetEpics(useEpicSearchParams())
  const { data: allTask = [] } = useGetTasks({projectId: currentProject?.id})
  const { mutate: deleteEpic } = useDeleteEpic(useEpicsQueryKey())
  const [ isCreateEpicOpen, setCreateEpicOpen ] = useState(false)
  const deleteEpicConfirm = (epic: Epic) => {
    Modal.confirm({
      title: `确定删除任务组: ${epic.name}?`,
      okText: '确定',
      cancelText: '取消',
      onOk: () =>{
        deleteEpic({ id: epic.id })
      }
    })
  }

  return <ScreenContainer>
    <JRow between={true}>
      <h1>{currentProject?.name}任务组</h1>
      <Button onClick={() => setCreateEpicOpen(true)}>创建任务组</Button>
    </JRow>
    <List
      style={{ overflow: 'scroll '}}
      dataSource={epicData}
      itemLayout={'vertical'}
      renderItem={epic => <List.Item>
        <List.Item.Meta
          title={<JRow between={true}>
            <span>{epic.name}</span>
            <Button type={'link'} onClick={() => deleteEpicConfirm(epic)}>删除</Button>
          </JRow>}
          description={
            <div>
              <div>开始时间：{dayjs(epic.start).format('YYYY-MM-DD')}</div>
              <div>结束时间：{dayjs(epic.end).format('YYYY-MM-DD')}</div>
            </div>
          }
        />
        <Row>
          {allTask
            .filter(task => task.epicId === epic.id)
            .map((task, index, arr) => (
              <div key={task.id}>
                <Link to={`/projects/${currentProject?.id}/kanban?editingTaskId=${task.id}`} >{task.name}</Link>
                {index < arr.length - 1 ? <Divider type="vertical" /> : null}
              </div>
            ))
          }
        </Row>
      </List.Item>}
    />
    <CreateEpic visible={isCreateEpicOpen} onClose={() => setCreateEpicOpen(false)}/>
  </ScreenContainer>
}