import { useMemo } from "react"
import { useProject } from "../../hooks/project"
import { cleanObj } from "../../utils"
import { useUrlQueryParam } from "../../utils/url"

export const useProjectsSearchParams = () => {
  const [ param, setParam ] = useUrlQueryParam(['name', 'personId'])
  const projectsParam = useMemo(() => {
    return cleanObj({ ...param, personId: Number(param.personId) || undefined })
  }, [ param ])

  return [
    projectsParam,
    setParam
  ] as const
}

export const useProjectQueryKey = () => {
  const [param] = useProjectsSearchParams()
  return ['projects', param]
}

export const useProjectModal = () => {
  const [projectState, setProjectState] = useUrlQueryParam([ 'isCreate', 'id' ])

  const { data: editingProjectData, isFetching } = useProject(Number(projectState.id))

  const open = () => setProjectState({ ...projectState, isCreate: true })
  const close = () => setProjectState({ ...projectState, isCreate: undefined, id: undefined})
  const startEdit = (id: number) => setProjectState({ ...projectState, id })

  return {
    projectModelOpen: projectState.isCreate === 'true' || !!projectState.id,
    open,
    close,
    startEdit,
    editingProjectData,
    isFetching
   } as const
}