import styled from "@emotion/styled"
import { List, Popover, Typography } from "antd"
import { useUsers } from "../hooks/use-users"

export const UserPopover = () => {
  const { data: usersData, refetch: usersRefetch } = useUsers()
  const content = <Content>
    <Typography.Text type={'secondary'}>组员列表</Typography.Text>
    <List>
      { usersData?.map(user => <List.Item key={user.id}>
        <List.Item.Meta title={user.name}/>
      </List.Item>)}
    </List>
  </Content>

  const onVisibleChange = (visible: boolean) => {
    if (visible) usersRefetch()
  }

  return <Popover onVisibleChange={onVisibleChange} placement={'bottom'} content={content}>
    <h3 style={{cursor: 'pointer'}}>组员</h3>
  </Popover>
}

const Content = styled.div`
  min-width: 30rem;
`