import { Link, Route, Routes, Navigate, useMatch, useLocation } from "react-router-dom"
import { KanbanScreen } from "../kanban"
import { EpicScreen } from "../epic"
import styled from "@emotion/styled"
import { Menu, MenuProps } from "antd"

const asideMenuItems = [
  {
    label: <Link to={'kanban'}>看板</Link>,
    key: 'kanban',
  },
  {
    label: <Link to={'epic'}>任务组</Link>,
    key: 'epic',
  }
]

const useRoutePath = () => {
  const units = useLocation().pathname.split('/')
  return units[units.length - 1]
}

export const ScreenProject = () => {
  const routeType = useRoutePath()
    return <Container>
    <Aside>
      <Menu
        items={asideMenuItems}
        mode={'inline'}
        selectedKeys={[routeType]}/>
    </Aside>
    <Main>
      <Routes>
        <Route path="/kanban" element={<KanbanScreen />} />
        <Route path="/epic" element={<EpicScreen />} />
        <Route path="/" element={<Navigate to="kanban" replace={true}/>} />
        {/* <Navigate to={window.location.pathname + '/kanban'}/> */}
      </Routes>
    </Main>
  </Container>
}

const Aside = styled.div`
  background-color: rgba(244, 245, 247);
  display: flex;
`

const Main = styled.div`
  display: flex;
  overflow: hidden;
`

const Container = styled.div`
  display: grid;
  grid-template-columns: 16rem 1fr;
  flex: 1;
  overflow: hidden;
`