import { QueryKey, useMutation, useQuery } from "react-query"
import { Task } from "../types/task"
import { useHttp } from "../utils/http"
import { useAddConfig, useDeleteConfig, useEditConfig, useReorderTaskConfig } from "./use-optimistic-options"

export const useGetTasks = (param?: Partial<Task>) => {
  const client = useHttp()
  
  return useQuery<Task[]>(['tasks', param], () => client('tasks', { data: param }))

}

export const useAddTask = (queryKey: QueryKey) => {
  const client = useHttp()

  return useMutation((params: Partial<Task>) => 
    client('tasks', {
      method: 'POST',
      data: params
    }),
    useAddConfig(queryKey)
  )
}

export const useGetTask = (id: number) => {
  const client = useHttp()
  return useQuery<Task>(
    ['task', id],
    () => client(`tasks/${id}`),
    {
      enabled: !!id
    }
  )
}

export const useEditTask = (queryKey: QueryKey) => {
  const client = useHttp()
  return useMutation((params: Partial<Task>) => client(`tasks/${params.id}`, {
    method: 'PATCH',
    data: params
  }),
    useEditConfig(queryKey)
  )
}

export const useDeleteTask = (queryKey: QueryKey) => {
  const client = useHttp()

  return useMutation(
    ({ id }: { id: number }) => client(`tasks/${id}`, {
      method: 'DELETE',
    }),
    useDeleteConfig(queryKey)
  )
}

interface ReorderTaskProps {
  type: 'after' | 'before',
  fromId: number,
  referenceId: number,
  fromKanbanId?: number,
  toKanbanId?: number
}

export const useReorderTask = (queryKey: QueryKey) => {
  const client = useHttp()

  return useMutation(
    (params: ReorderTaskProps) => client('tasks/reorder', {
      method: 'POST',
      data: params
    }),
    useReorderTaskConfig(queryKey)
  )
}