import { useMemo } from "react"
import { URLSearchParamsInit, useSearchParams } from "react-router-dom"
import { cleanObj } from "."

export const useUrlQueryParam = <K extends string>(keys: K[]) => {
  let [ searchParams, setSearchParams ] = useSearchParams()
  const pickQueryParams = useMemo(() => keys.reduce((acc, key) => {
    return searchParams.has(key) ? {...acc, ...{ [key]: searchParams.get(key) }} : acc
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, {} as {[key in K]: string }), [ searchParams ])

  const setParam = (params: Partial<{[key in K]: unknown }>) => {
    const o = cleanObj({ ...Object.fromEntries(searchParams), ...params}) as URLSearchParamsInit
    setSearchParams(o)
  }


  return [
    pickQueryParams,
    setParam
  ] as const
}

