
import { useHttp } from "../utils/http"
import { Project } from "../types/project"
import { QueryKey, useMutation, useQuery } from "react-query"
import { useEditConfig, useAddConfig, useDeleteConfig } from './use-optimistic-options'

export const useGetProjects = (param?: Partial<Project>) => {
  const client = useHttp()
  return useQuery<Project[]>(['projects', param], () => client('projects', { data: param }))

}

export const useEditProject = (queryKey: QueryKey) => {
  const client = useHttp()

  return useMutation(
    (params: Partial<Project>) => client(`projects/${params.id}`, {
      method: 'PATCH',
      data: params
    }), 
    useEditConfig(queryKey)
  )
}

export const useAddProjects = (queryKey: QueryKey) => {
  const client = useHttp()

  return useMutation(
    (params: Partial<Project>) => client(`projects`, {
      method: 'POST',
      data: params
    }), 
      useAddConfig(queryKey)
    )
}

export const useDeleteProjects = (queryKey: QueryKey) => {
  const client = useHttp()

  return useMutation(
    ({ id }: { id: number }) => client(`projects/${id}`, {
      method: 'DELETE'
    }), 
      useDeleteConfig(queryKey)
    )
}

export const useProject = (id: number) => {
  const client = useHttp()
  const result = useQuery<Project>(
    ['project', id],
    () => client(`projects/${id}`),
    {
      enabled: !!id
    }
  )
  return result
}