import { Form, Input, Modal } from "antd"
import { useForm } from "antd/lib/form/Form"
import { useEffect } from "react"
import { TaskTypeSelect } from "../../components/task-type-select"
import { UserSelect } from "../../components/user-select"
import { useEditTask } from "../../hooks/task"
import { useTaskModal, useTaskQueryKey } from "./utils"

export const TaskModal = () => {
  const { editingTaskData, close, isOpen} = useTaskModal()
  const [form] = useForm()
  const { mutateAsync: editTask, isLoading: isEditLoading } = useEditTask(useTaskQueryKey())

  const onCancel = () => {
    close()
    form.resetFields()
  }

  const onOk = async () => {
    await editTask({ ...editingTaskData, ...form.getFieldsValue()})
    close()
  }

  useEffect(() => {
    form.setFieldsValue(editingTaskData)
  }, [editingTaskData, form])


  return (
    <Modal
      onOk={onOk}
      onCancel={onCancel}
      okText="确认"
      cancelText="取消"
      title="编辑任务"
      confirmLoading={isEditLoading}
      visible={isOpen}
      forceRender={true}
    >
     <Form {...layout} initialValues={editingTaskData} form={form}>
      <Form.Item rules={[{required: true, message: '请输入任务名'}]} label="任务名" name={'name'}>
        <Input placeholder="任务名"/>
      </Form.Item>
      <Form.Item label="经办人" name={'processorId'}>
        <UserSelect defaultOptionName="经办人"/>
      </Form.Item>
      <Form.Item label="类型" name={'typeId'}>
        <TaskTypeSelect defaultOptionName="类型"/>
      </Form.Item>
    </Form>   
    </Modal>
  )
}

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 }
}