
import {  Dropdown, Menu, Modal, Table, TableProps } from 'antd'
import type { MenuProps } from 'antd';
import { Project } from '../../types/project'
import { User } from '../../types/user';
import type { ColumnsType } from 'antd/es/table';
import dayjs from 'dayjs';
import { Link } from 'react-router-dom';
import { Pin } from '../../components/pin';
import { useDeleteProjects, useEditProject } from '../../hooks/project';
import { ButtonNoPadding } from '../../components/lib';
import { useProjectModal, useProjectQueryKey } from './utils';


interface ListProps extends TableProps<Project> {
  users: User[],
  refresh?: () => void
}

export const List = ({ users, ...props }: ListProps) => {
  const { mutate } = useEditProject(useProjectQueryKey())
  const pinProject = (id: number) => (pin: boolean) => mutate({ id, pin })


  const columns: ColumnsType<Project> = [
    {
      title: <Pin checked={true} disabled={true}/>,
      render: ({ id, pin }) => <Pin checked={pin} onCheckedChange={pinProject(id)}/>
    },
    {
      title: '名称',
      dataIndex: 'name',
      key: 'name',
      sorter: (a, b) => a.name.localeCompare(b.name),
      render: (name, project) => {
        return <Link to={String(project.id)}>{name}</Link>
      }
    },
    {
      title: '部门',
      dataIndex: 'organization',
      key: 'organization',
    },
    {
      title: '负责人',
      dataIndex: 'personId',
      key: 'personId',
      render: id => users.find(user => user.id === id)?.name || 'unknown',
    },
    {
      title: '创建时间',
      dataIndex: 'created',
      key: 'created',
      render: time => time ? dayjs(time).format('YYYY-MM-DD') : '无',
    },
    {
      render: project => <More project={project}/>
    }
  ]

  return (
    <Table rowKey="id" pagination={false} columns={columns} {...props} />
  )
}

const More = ({ project }: { project: Project}) => {
  const { startEdit } = useProjectModal()
  const editProject = (id: number) => () => startEdit(id)
  const { mutate: deleteProject } = useDeleteProjects(useProjectQueryKey())
  const confirmDeleteProject = (id: number) => {
    Modal.confirm({
      title: '确定删除这个项目吗？',
      content: '点击确定删除',
      okText: '确定',
      cancelText: '取消',
      onOk: () => {
        deleteProject({ id })
      }
    })
  }
  const menuItems: MenuProps['items'] =[
    { 
      label: '编辑',
      key: 'edit',
      onClick: editProject(project.id)
    },
    { 
      label: '删除',
      key: 'delete',
      onClick: () => confirmDeleteProject(project.id)
    }
  ]

  return (
    <Dropdown
          overlay={
            <Menu items={menuItems} />
          }>
      <ButtonNoPadding type={'link'}>...</ButtonNoPadding>
    </Dropdown>
  )
}