import styled from "@emotion/styled"
import { Button, Drawer, DrawerProps, Form, Input } from "antd"
import { useForm } from "antd/lib/form/Form"
import { useAddEpic } from "../../hooks/epic"
import { Epic } from "../../types/epic"
import { useProjectIdInUrl } from "../kanban/utils"
import { useEpicsQueryKey } from "./utils"

export const CreateEpic = (props: Pick<DrawerProps, 'visible'> & { onClose: () => void}) => {
  const { mutate: addEpic, isLoading: mutateLoading } = useAddEpic(useEpicsQueryKey())
  const [form] = useForm()
  const projectId = useProjectIdInUrl()
  const onFinish = async (values: Partial<Epic>) => {
    await addEpic({...values, projectId })
    props.onClose()
  }


  return <Drawer
    visible={props.visible}
    onClose={props.onClose}
    forceRender={true}
    destroyOnClose={true}
    width={'100%'}
  >
    <Container>
      <h1>创建任务组</h1>
      <Form form={form} layout={'vertical'} style={{ width: '40rem'}} onFinish={onFinish}>
        <Form.Item label={'名称'} name={'name'} rules={[{ required: true, message: '请输入任务组名称'}]}>
          <Input placeholder="请输入任务组名称" />
        </Form.Item>

        <Form.Item>
          <Button loading={mutateLoading} type={'primary'} htmlType={'submit'}>
            提交
          </Button>
        </Form.Item>
      </Form>
    </Container>
  </Drawer>
}

const Container = styled.div`
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
`