import styled from "@emotion/styled"
import { Divider, List, Popover, Typography } from "antd"
import { useGetProjects } from "../hooks/project"
import { useProjectModal } from "../screens/project-list/utils"
import { ButtonNoPadding } from "./lib"

export const ProjectPopover = () => {
  const { data: projectData, refetch: projectRefetch } = useGetProjects({})
  const { open } = useProjectModal()
  const pinnedProjects = projectData?.filter(project => project.pin)
  const content = <Content>
    <Typography.Text type={'secondary'}>收藏项目</Typography.Text>
    <List>
      { pinnedProjects?.map(project => <List.Item key={project.id}>
        <List.Item.Meta title={project.name}/>
      </List.Item>)}
    </List>
    <Divider/>
    <ButtonNoPadding type={"link"} onClick={open}>创建项目</ButtonNoPadding>
  </Content>

  return <Popover placement={'bottom'} content={content}>
    <h3 style={{cursor: 'pointer'}}>项目</h3>
  </Popover>
}

const Content = styled.div`
  min-width: 30rem;
`