import { Select,  } from "antd"
import { Raw } from "../types"

type SelectProps = React.ComponentProps<typeof Select>

type SelectOmitProps =  'value' | 'defaultOptionName' | 'options' | 'onChange' 

interface IdSelectProps extends Omit<SelectProps, SelectOmitProps> {
  value?: Raw | undefined | null,
  defaultOptionName: string,
  options?: {name: string, id: number}[],
  onChange?: (value?: number) => void
}

export const IdSelect = (props: IdSelectProps) => {
  const { value, onChange, defaultOptionName, options, ...restProps } = props
  const { Option } = Select
  return (
    <Select
      value={options?.length ? toNumber(value) : 0}
      onChange={(value) => onChange?.(toNumber(value)) }
      {...restProps}>
      {
        defaultOptionName ? <Option value={0}>{defaultOptionName}</Option> : null
      }
      {
        options?.map(option => <Option key={option.id} value={option.id}>{option.name}</Option>)
      }
    </Select>
  )
}

const toNumber = (value: any) => isNaN(Number(value)) ? 0 : Number(value)
