import React from 'react'
import { Kanban } from "../../types/kanban";
import { BugTwoTone, WalletTwoTone } from '@ant-design/icons';
import { useTaskTypes } from "../../hooks/task-type";
import styled from "@emotion/styled";
import { Button, Card, Dropdown, Menu, Modal, Space } from "antd";
import type { MenuProps } from 'antd';
import { Task } from "../../types/task";
import { CreateTask } from "./create-task";
import { useKanbanQueryKey, useTaskModal, useTaskQueryKey, useTaskSearchParams } from "./utils";
import { Mark } from "../../components/mark";
import { useDeleteKanban } from "../../hooks/kanban";
import { DeleteTwoTone } from '@ant-design/icons'
import { FormEvent } from "react";
import { useDeleteTask } from "../../hooks/task";
import { Drag, Drop, DropChild } from '../../components/drag-and-drop';

export const KanbanColumn = React.forwardRef<HTMLDivElement, { kanban: Kanban, allTasks: Task[] }>(({ kanban, allTasks, ...props }, ref) => {
  // 通过 kanban.id 过滤需要的任务列表
  const tasks = allTasks?.filter(task => task.kanbanId === kanban.id)

  return <ColumnContainer
    {...props} ref={ref}
    hoverable={true}
    title={kanban.name}
    size={'small'}
    headStyle={{backgroundColor: '#1890ff', color: '#fff'}}
    bodyStyle={{ flex: 1, overflowX: 'scroll'}}
    extra={<More kanban={kanban} key={kanban.id}/>}>
    <TaskContainer>
      <Drop type={'ROW'} direction={'vertical'} droppableId={String(kanban.id)}>
        <DropChild style={{minHeight: '5px'}}>
          {tasks?.map((task, taskIndex) => (
            <Drag key={task.id} index={taskIndex} draggableId={String(task.id)}>
              <div>
                <TaskCard key={task.id} task={task}/>
              </div>
            </Drag>
          ))}
        </DropChild>
      </Drop>
    </TaskContainer>
    <CreateTask kanbanId={kanban.id}/><CreateTask kanbanId={kanban.id}/>
  </ColumnContainer>
})

const TaskTypeIcon = ({ id }: { id: number }) => {
  const { data: taskTypes } = useTaskTypes()
  const name = taskTypes?.find(taskType => taskType.id === id)?.name

  if (!name) return null

  const bugIcon = <BugTwoTone twoToneColor="#eb2f96"/>
  const taskIcon = <WalletTwoTone />

  return name === 'task' ? taskIcon : bugIcon
}

const TaskCard = ({ task }: { task: Task }) => {
  const { startEdit } = useTaskModal()
  const [{ name: keyword }] = useTaskSearchParams()
  const { mutate: deleteTask } = useDeleteTask(useTaskQueryKey())
  const deleteConfirm = (e: FormEvent) => {
    e.stopPropagation()
    Modal.confirm({
      title: '确定删除该任务?',
      okText: '确定',
      cancelText: '取消',
      onOk() {
        deleteTask({ id: task.id })
      },
    })    
  }

  return (
    <CardContainer
      key={task.id}
      bodyStyle={{ display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between'
      }}>
      <CardBody onClick={() => startEdit(task.id)}>
        <Space>
          <TaskTypeIcon id={task.typeId}/>
          <Mark text={task.name} keyword={keyword}/>
          {'id: ' + task.id}
        </Space>
      </CardBody>
      <DeleteTwoTone onClick={deleteConfirm}/>
    </CardContainer>
  )
}

const More = ({kanban}: {kanban: Kanban}) => {
  const { mutate } = useDeleteKanban(useKanbanQueryKey())
  const deleteConfirm = () => {
    Modal.confirm({
      title: '确定删除该看板?',
      okText: '确定',
      cancelText: '取消',
      onOk() {
        mutate({ id: kanban.id })
      },
    })
  }
  const MenuItems: MenuProps['items'] = [
    {
      label: <Button type={'link'} onClick={deleteConfirm}>删除</Button>,
      key: 'delete',
    },
  ]

  return (
    <Dropdown overlay={<Menu items={MenuItems}/>}>
      <Button type={'link'}>...</Button>
    </Dropdown>
  )
}

export const ColumnContainer = styled(Card)`
  min-width: 28rem;
  position: relative;
  margin-right: 1.5rem;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
`
const TaskContainer = styled.div`
  flex: 1;
  overflow: scroll;

  ::-webkit-scrollbar {
    display: none;
  }
`

const CardContainer = styled(Card)`
  margin-bottom: 0.5rem;
  cursor: pointer;
`

const CardBody = styled.div`
  flex: 1;
`