import styled from "@emotion/styled"
import { Spin } from "antd"
import { ScreenContainer } from "../../components/lib"
import { useDebounce } from "../../hooks/hooks"
import { useGetKanbans } from "../../hooks/kanban"
import { useGetTasks } from "../../hooks/task"
import { useDocumentTitle } from "../../hooks/use-document-title"
import { CreateKanban } from "./create-kanban"
import { KanbanColumn } from "./kanban-column"
import { SearchPanel } from "./search-panel"
import { TaskModal } from "./task-modal"
import { useDragEnd, useKanbanSearchParams, useProjectInUrl, useTaskSearchParams } from "./utils"
import { DragDropContext } from 'react-beautiful-dnd'
import { Drag, Drop, DropChild } from "../../components/drag-and-drop"

export const KanbanScreen = () => {
  // 通过 projectId 获取看板列表
  const { data: kanbans, isLoading: isKanbanLoading } = useGetKanbans(useKanbanSearchParams())
  // 通过 projectId、筛选条件 获取任务列表
  const [searchParams] = useTaskSearchParams()
  const { isLoading: isTaskLoading, data: allTasks } = useGetTasks(useDebounce(searchParams, 200))
  const isLoading = isKanbanLoading || isTaskLoading
  // 获取当前 project
  const { data: currentProject } = useProjectInUrl()

  const onnDragEnd = useDragEnd()
  useDocumentTitle('看板列表')

  return (
    <DragDropContext onDragEnd={onnDragEnd}>
      <ScreenContainer>
        <h1>{currentProject?.name}看板</h1>
        <SearchPanel />
        { isLoading ? <Spin size={'large'}/> : (
        <ColumnsContainer>
          <Drop type={'COLUMN'} direction={'horizontal'} droppableId={'kanban'}>
            <DropChild style={{display: 'flex'}}>
            {
              kanbans?.map((kanban, index) => (
                <Drag key={kanban.id} draggableId={'kanban' + kanban.id} index={index}>
                  <KanbanColumn kanban={kanban} allTasks={allTasks || []} key={kanban.id} />
                </Drag>
              ))
            }
            </DropChild>
          </Drop>
          <CreateKanban />
        </ColumnsContainer>
        )}
        <TaskModal />  
      </ScreenContainer>
    </DragDropContext>
  )
}

const ColumnsContainer = styled.div`
  display: flex;
  flex: 1;
  padding: 10px;
  overflow-x: scroll;
`
