import { Typography } from "antd"

export const Mark = ({text, keyword}: {text: string, keyword: string }) => {
  if (!keyword) {
    return <>{text}</>
  }

  const arr = text.split(keyword)
  return <>
    {arr.map((str, index) => <span key={index}>
      { str }
      { index === arr.length - 1 ? null : <Typography.Text mark>{keyword}</Typography.Text> }
    </span>
    )}
  </>
}